*{
  box-sizing: border-box;
  font-family: "Gotham", "Helvetica Neue", Helvetica, Arial, sans-serif;
  color: #393c41;
  margin: 0;
  padding: 0;
  
}
body{
  width: 100%;
  overflow-x:hidden ;
  

}




@keyframes animatedown {
  0%,20%,50%,80%,100%{
    transform: translateY(0);
  }
  40%{
    transform: translateY(5px);
  }
  60%{
    transform: translateY(3px);

  }
}